			}
		},
		onShow(){
			this.oid = uni.getStorageSync("openid")
			this.cyy()
		//	this.zjj()

		},
		methods: {
			bback(){
				uni.navigateBack(-1)
			},
			zjj(){
				uni.showModal({
					title: '提示',
					content: "请返回 ’我的' 页面联系客服领取奖品",
					success: function (res) {
						if (res.confirm) {
							uni.switchTab({
								url:"/pages/mine/mine"
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			},
			cyy(){
				uni.request({
					method:"POST",
					url:this.apiUrl+"index.php/index/grzjjp",
					data:JSON.stringify({
						"openid":uni.getStorageSync("openid")
					}),
					success:(res)=>{
							console.log(res)
							this.cyspInfos=res.data
							//this.cyspInfo = res.data
							console.log("cyp")
							console.log(this.cyspInfos)
							
					}
